<template>
	<div class="default-panel" id="proList">
		<addressBar
			:isBack = 'true'
			:navBtnShow = 'true'
			:goBackLink = "'/'"
			v-on:navShowClick = 'navShowHandle'

		></addressBar>
			<div class="proList-panel" :style="{ height: wrapperHeight + 'px' }" ref="wrapper">
  				<mt-loadmore 
	  				:bottom-method="bottomTop" 
	  				@bottom-status-change="handleBottomChange" 
	  				:bottom-all-loaded="allLoaded"
	  				:autoFill = "false"
                	ref = "loadmore"
  				>

				  	<div class="pro-group first">
						<div class="pro-item" v-for="item in proList">
							<router-link :to="{ path: '/productdetail/'+item.productId, query: { url: router }}">
								<img :src="item.imgUrl" alt="">
								<div class="pro-details">
									<span class="title">{{item.displayName}}</span>
									<span class="price">¥{{item.discountPrice | filterPrice}}</span>
								</div>
							</router-link>
						</div>
					</div>
					<div slot="bottom" class="mint-loadmore-bottom">
		                <span v-show="bottomStatus !== 'loading'" :class="{ 'rotate': bottomStatus === 'pull' }">↑</span>
		              	<span v-show="bottomStatus === 'loading'">Loading...</span>
		            </div>
				</mt-loadmore>
			</div>	
		<mt-popup
			v-model="navShow"
			position = 'right'
			:closeOnClickModal = "true"
			class = 'right-nav-wap'
		>	
			
			<span class="icon-nav-group">
				<span class="icon-nav" @click="navShowHandle"></span>
			</span>
			
			<rightNavList></rightNavList>
		</mt-popup>

	</div>
</template>
<script>
	import Vue from 'vue';
	import { Popup } from 'mint-ui';
	Vue.component(Popup.name, Popup);

	import { Loadmore } from 'mint-ui';
	Vue.component(Loadmore.name, Loadmore);

	
	import { mapState } from 'vuex'
	import addressBar from '@/components/common/navbar/adressBar'
	import rightNavList from '@/components/home/home/classifyNavList'

	// 注册
	Vue.filter('filterPrice', function (value) {
	 	return (value/100).toFixed(2);
	})
	
	export default {
		data(){
			return {
				bottomStatus: '',
        		allLoaded: false,
        		wrapperHeight: '600'
			}
		},
		computed: {
			router(){
				return this.$route.path;
			},
			...mapState({
				classifyNav: state => state.homeState.classifyNav,
				proList: state => state.homeState.classifyProList,
				navShow: state => state.homeState.navShow,
				pageNo: state => state.homeState.classifyPage,
				totalPage: state => state.homeState.classifyTotalPage,

			})
		},
		watch:{
			pageNo(val){
				this.allLoaded = false;
				if(val  >= this.totalPage) {
					this.allLoaded =  true; 
				}else{
					this.allLoaded =  false; 
				}
			},
			router(val,oldVal){
				this.getProList();
			}
		},
		components: {
			addressBar,
			rightNavList
		},
		methods: {
			navShowHandle() {
				this.$store.dispatch('navShowToggle');
			},
			getProList(){
				var self = this;
				let id = this.$route.params.id ? this.$route.params.id  : '';
				let parentId = this.$route.params.parentId ? this.$route.params.parentId  : '';
				self.$store.dispatch('getClassifyProList',{
					categoryId: id,
					fristCategoryId: parentId
				}).then(function(){

				});
			},
			bottomTop() {
				this.getProList();
		        this.$refs.loadmore.onBottomLoaded();
		    },
	        handleBottomChange(status) {
	            this.bottomStatus = status;
	        }
		},
		mounted(){
			let self = this;
			self.getProList();
			self.wrapperHeight = document.documentElement.clientHeight - self.$refs.wrapper.getBoundingClientRect().top;
		},
		destroyed(){
			this.$store.dispatch('getCategory');
		}
	}
</script>

<style lang="scss">
	$baseFontSize: 750px;
	@function pxToRem($px) {
		@return $px / $baseFontSize * 10rem;
	}

	.proList-panel {
		padding: 0 pxToRem(55px);
		.pro-group {
			padding: 0;
		}
	}

	.page-infinite-listitem {
	    height: 150px;
	    line-height: 150px;
	}
</style>


