<template>
	
<div>
	<div class="paySuc-sec">
		<div class="suc-img">
			<span class="pay-zfb-icon"></span>
			<img src="../../assets/images/payXfb.png" alt="">
		</div>
		<p class="tips">请在菜单中选择在浏览器中打开，<br/>以完成支付~</p>
		<div class="suc-operate-group">
			<span class="op-items" @click="checkPaySec(0)">已完成支付</span>
			<span class="op-items" @click="checkPaySec(1)">取消并返回</span>
		</div>
	</div>
</div>
</template>
<script>
	import normalBar from '@/components/common/navbar/normalBar'
	import pingpp from 'pingpp-js'
	import { mapState } from 'vuex'

	import api from '@/api/api'


	export default {
		computed: {
      isWxLogin () {
        return this.utils.isWxLogin()
      }
    },
		components: {
			normalBar
		},
		created(){
			let payNo = this.$route.params.payNo;
			if(!this.isWxLogin){
				api.post('api/appclient/v1/payment/payCharge',{
					cancelUrl: '/home' ,//	取消支付返回的url	string	
					payNo: payNo,//	支付号	string	
					successUrl:'/home',
					payType: 2

				},function(charge){
					pingpp.createPayment(charge, function(result, err){
					  
					});
				})
			}
		},
		methods:{
			checkPaySec(type){
				let self = this;
				let payNo = this.$route.params.payNo;

				// self.$router.push({
				// 	path: '/orderDetails/'+self.merchantId
				// });
				
				api.post('api/appclient/v1/payment/chargeResult',
					{
						payNo: payNo
					},function(json){
						alert(json);
						let res = json.body;
						self.success = res.result;
						self.mchId = res.merchantId

						if(res.result){
							self.$route.push({
								path: '/paySuccess/'+payNo
							})
						}else {
							self.$route.push({
								path: '/orderDetails/'+res.merchantId
							})
						}
					}
				)

			}
		}

	}
</script>
<style lang="scss">
	$baseFontSize: 750px;
	@function pxToRem($px) {
		@return $px / $baseFontSize * 10rem;
	}
	$defaultSize: pxToRem(24px);
	
	.paySuc-sec {
		text-align: center;
		color: #9B9B9B;
		
		.tips {
			display: block;
			margin-top: pxToRem(40px);
		
		}
	}
	.suc-img {
		margin-top: pxToRem(160px);
		display: inline-block;
		width: pxToRem(320px);
		height: pxToRem(320px);
		color: #fff;
		position: relative;
		
		.pay-zfb-icon {
			display: inline-block;
			width: pxToRem(108px);
			height: pxToRem(108px);
			background: url('../../assets/images/payBack.png') no-repeat center center;
			background-size: 100%;
			position: absolute;
			right: - pxToRem(40px);
			top: 30%;
			margin-top: - pxToRem( 54px);
		}
		.text {
			display: block;
			width: 100%;
			position: absolute;
			top: pxToRem(50px);
			left: 0;
			font-size: pxToRem(28px);
		}
		img {
			width: 100%;	
		}
	}

	.suc-operate-group {
		margin-top: pxToRem(100px);
		font-size: 0;
		.op-items {
			display: inline-block;
			background-color: #fff;
			padding: pxToRem(20px) pxToRem(50px);
			border: 1px solid #666;
			font-size: pxToRem(24px);
			line-height: 1;
			color: #191919;

			&:not(:last-of-type) {
				margin-right: pxToRem(60px);
			}

		}
	}

	

</style>


