<template>

	<div id="home">
		<addressBar
			:isBack = "false"
			:goBackLink = "'/'"
		></addressBar>

    <div>{{respond}}</div>
    <div class="banner-group ">
      <mt-swipe :auto="3000">
        <mt-swipe-item class="banner-slide" v-for="banner in bannerDatas">
          <img :src="banner.src" alt="" @click="goBannerEvent(banner)">
        </mt-swipe-item>
      </mt-swipe>
    </div>

    <div class="home-content">
      <div v-for="key in Object.keys(datas)">

        <div class="home-content-cellHeader">
          <span class="home-content-typeName">{{typeNames[key]}}</span>
          <span>
            <span @click="goClassify(key)" class="home-content-rightText">查看全部</span>
            <span class="icon-arrow-right home-content-right "></span>
          </span>
        </div>
        <hr style="height: 1px;background-color: #cccccc;" class="home-content-cellHeaderHr"/>

        <div class="home-content-cellContent">
          <span v-for="product in datas[key]" class="home-content-cell">
            <router-link :to="'/productdetail/' + product.productId">
            <div>
                <img src="" class="home-content-cellImg"/>
              <!--<img :src="product.imgUrl" class="home-content-cellImg"/>-->
              </div>
              <div class="home-content-cellNameText"><a style="overflow:hidden;text-overflow: ellipsis;" class="home-content-cellNameText">{{product.displayName}}</a> </div>
              <div class="home-content-cellPriceText">￥{{product.discountPrice/100}}</div>
            </router-link>
          </span>

        </div>

      </div>
    </div>

    <div class="home-footer" style="height: 60px"></div>

	</div>

  
</template>
<script>

  import mapHelper from '@/js/mapHelper'
  import api from '@/api/common/api.common'
  import http from '@/api/api.js'
  import homeApi from '@/api/home/api.home'
  import bus from '../../../js/eventBus'
	import addressBar from '@/components/common/navbar/adressBar'
  import axios from 'axios'

  	export default {
	    name: 'homeIndex',
	    props: [],
	    data () {
	      return {
          bTrue: true,
          typeNames: ['','蛋糕','下午茶','手信'],
          datas: '',
          cityData: undefined,
          localPosition: undefined,
          bannerDatas: [],
          productType: undefined,
          respond: '666'
	      }
	    },
	    components: {
	    	addressBar
	    },
	    methods: {
	      getProductType () {
	        var self = this
          api.getProductType({},function (respond) {
            var types = respond.body.data['0']
            var productType = {}
            types.forEach(function (type) {
              productType[type.type] = type.id
            })
            self.productType = productType
          })
        },
        goBannerEvent (banner) {
          this.$router.push(banner.url)
        },
	      getAllData () {
          var self = this
          //查看全部的蛋糕类型
          this.getProductType()
	        // 获取轮播图
	        this.getBannerData()
          // 获取蛋糕list
          this.getCakeList(self)
        },
	      getBannerData () {

	        var self = this
	        homeApi.getBanner({},function (respond) {
            self.bannerDatas = respond.body.data
          })

        },
	      initData () {
	        var self = this
          var city = {cityId:'440300', id:"440300", name:"深圳市", cityName:"深圳市", provinceName: "广东省"}
          var data = {}
          data.regionlismCityId = city.id
          api.cacheLocation(data,function (respond) {
//            self.$store.dispatch('setUserCity' , city)
            localStorage.setItem('cacheKey', respond.body.data['cacheKey']);
            bus.$emit('changeCity',city)
            self.getCakeList(self)
          })
        },
	      getCakeList (self) {
          homeApi.getHomeCakeList({},function (respond) {
            self.datas = respond.body.data
          })
        },
	      getCityData () {
	        var self = this
          if (this.hasGetLocation()) {
	          this.getCakeList(this)
          } else {
            api.getAllXFCity({},function (respond) {
              self.cityData = respond.body.data
              if (self.localPosition !== undefined) {
                self.autoSelectCity(self)
              }
            })
          }

        },
        cacheCity(city,self){
          var data = {}
          data.regionlismCityId = city.regionalismId
          api.cacheLocation(data,function (respond) {
            console.log(respond)
            self.$store.dispatch('setUserCity' , city)
            localStorage.setItem('cacheKey', respond.body.data['cacheKey'])
            var cityStr = JSON.stringify(city)
            localStorage.setItem('locationCity', cityStr)
            bus.$emit('changeCity',city)
            if (city.cityName !== '深圳市') {
              self.getCakeList(self)
            }
          })
        },
	      autoSelectCity (self) {

          var searchCityCount = 0
          var searchCity = undefined
          self.cityData.forEach(function (city) {
            if (city.isCity === 1) {
              if (city.cityName === self.localPosition.city) {
                searchCityCount++
                searchCity = city
              }
            }
          })

          if (searchCityCount === 1) {
            self.cacheCity(searchCity,self)
          } else {
            self.$router.push('/chooseCity')
          }

        },
	      goClassify (key) {
	      	// this.$store.dispatch('getCategory',{id: id});
          var id = this.productType[key]
	      	this.$router.push({
	      		path: 'productList/parentId/'+id
	      	})
	      },
        getLocalPosition () {
          // 定位
          var self = this
          mapHelper.getCurrentLocation(function (address) {
            self.localPosition = address
            if (self.cityData !== undefined) {
              self.autoSelectCity(self)
            }
          })
        },
        dealWXSign () {
          if (this.utils.isWxLogin()) {
            return
          }

          var code = this.utils.getUrlKey('code')
          var self = this
          if (code !== null) {
            var data = {}
            data.code = code
            api.wxAutoSign(data,function (respond) {

              localStorage.setItem('isWxLogin-cache', true);
//              localStorage.setItem('userIsLogin', true);
              localStorage.setItem('userloginTime', new Date());
              localStorage.setItem('loginCredentials', respond.body.data['loginCredentials']);
            })
          }
        },
        hasGetLocation () {
          console.log(localStorage.getItem('locationCity'))
          if (localStorage.getItem('locationCity') === null) {
            return false
          } else {
            return true
          }
        }
	    },
	    created () {
	     
	    },
	    computed: {
	      
	    },
	    mounted () {

        if (!this.hasGetLocation()) {
          // 初始化深圳商品
//          this.initData()
          // 定位
          this.getLocalPosition()
          //获取所有城市
//          this.getAllData()
        } else {
//          this.getAllData()
        }
	      // 处理微信登录
	      this.dealWXSign()

        var self = this
//        // http://apigateway.blissmall.net/api/front/v1/baseinfo/city/searchAllCity
//        this.$Vue.http.options.emulateJSON = true;
//        this.$Vue.http.get('/wx/connect/oauth2/authorize?appid=wx1d101c2fc4fb041b&redirect_uri=http%3A%2F%2Ftcjxm6.xyz%2FVueProject%2Findex.html&response_type=code&scope=snsapi_base#wechat_redirect',function (respond) {
//          self.respond = respond
//        })
//
//        this.$Vue.http.get('http://apigateway.blissmall.net/api/front/v1/baseinfo/city/searchAllCity',function (respond) {
//          self.respond = respond
//        })
        var url2 = '/wx/connect/oauth2/authorize?appid=wx1d101c2fc4fb041b&redirect_uri=http%3A%2F%2Ftcjxm6.xyz%2FVueProject%2Findex.html&response_type=code&scope=snsapi_base#wechat_redirect'
        var url = 'https://open.weixin.qq.com/connect/oauth2/authorize?appid=wx1d101c2fc4fb041b&redirect_uri=http%3A%2F%2Ftcjxm6.xyz%2FVueProject%2Findex.html&response_type=code&scope=snsapi_base#wechat_redirect'
        axios.get(url2).then(function (respond) {
          self.respond = respond
          console.log(respond)
        })

//        this.$Vue.http.get('https://open.weixin.qq.com/connect/oauth2/authorize?appid=wx1d101c2fc4fb041b&redirect_uri=http%3A%2F%2Ftcjxm6.xyz%2FVueProject%2Findex.html&response_type=code&scope=snsapi_base#wechat_redirect',function (respond) {
//          self.respond = respond
//        })

	    }
  	}
</script>

<style lang="scss">
	$baseFontSize: 750px;
	$defaultColor: #191919;
	@function pxToRem($px) {
		@return $px / $baseFontSize * 10rem;
	}
	$defaultSize: pxToRem(24px);

 	.banner-group {
 		/*width: pxToRem(690px);*/
 		height: pxToRem(740px);
    margin-left: pxToRem(30px);
    margin-right: pxToRem(30px);
 		.banner-slide {
 			img {
 				/*max-width: 100%;*/
        width: 100%;
        height: 100%;
 			}
 		}
 		.mint-swipe-indicator {
 			background-color: rgba(255,255,255,1);
 			opacity: 0.5;
 			transform: scale(.75);
 			&.is-active{
 				background-color: rgba(255,255,255,1);
 				opacity: 1;
 				transform: scale(1);
 			}
 		}
 	}

  .home-content-cellHeader {
    height: pxToRem(74px);
    margin-top: pxToRem(80px);
    margin-left: pxToRem(30px);
    margin-right: pxToRem(30px);


    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .home-content-cellHeaderHr {
    margin-left: pxToRem(30px);
    margin-right: pxToRem(30px);
    margin-bottom: pxToRem(40px);
  }

  .home-content-cellContent {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-wrap: wrap;
  }

  .home-content-cell {
    width: pxToRem(300px);
    height: pxToRem(416px);
    margin-right: pxToRem(20px);
    margin-left: pxToRem(20px);
    margin-bottom: pxToRem(60px);
  }
  .home-content-cellImg {
    width: pxToRem(300px);
    height: pxToRem(300px);
  }
  .home-content-cellNameText {
    margin-top: pxToRem(40px);
    font-size: pxToRem(24px);
    /*height: pxToRem(26px);*/
    text-align: center;
    color: black;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space:nowrap;
    width: pxToRem(300px);
  }
  .home-content-cellPriceText {
    font-size: pxToRem(24px);
    margin-top: pxToRem(4px);
    text-align: center;
    color: #d8331d;
  }
  .home-content-right {
    vertical-align: -4%;
  }
  .home-content-rightText {
    color: #BDBDBD;
    font-size: pxToRem(24px);
    margin-right: pxToRem(10px);
  }
  .home-content-typeName {
    color: #191919;
    font-size: pxToRem(24px);
  }
	
</style>



