<template>
	
<div class="default-panel">
	<normalBar title="收货地址" :path="backPath"></normalBar>
	<div class="empty-sec" v-if="empty" v-cloak>
		当前暂无收货地址
	</div>


	<div class="address-list" v-show="!!addressList" ref="wrapper" >

		<mt-loadmore 
	  				:bottom-method="bottomTop" 
	  				@bottom-status-change="handleBottomChange" 
	  				:bottom-all-loaded="allLoaded"
	  				:autoFill = "false"
                	ref = "loadmore"
  				>

			<mt-cell-swipe
				v-for=" (item,index) in addressList"
			  	:right="[
			  		{
				      	content: '设为默认',
				      	style: { background: '#D4B675', color: '#fff' },
				      	handler: () => {setDefaultAddress(item.id,index)}
		    		},
				    {
				      content: '删除',
				      style: { background: '#D8331D', color: '#fff' },
				      handler: () => {	delAddress(item.id,index)  }
		    		}
	  			]"
	  			
	  		>
				<div class="address-sec"
					@click="checkedToAddress(item)"
					:class="{disabled: item.checkStatus == 0 && isOrderAddress}"
				>
					<div class="address-content">
						<p class="title" v-if="item.isDefault">默认地址</p>
						<p class="big-size">{{item.recipientName}}<span class="last">收</span></p>
						<p>{{item.address}}</p>
						<p class="phone">{{item.recipientMobile}}</p>
					</div>
					<router-link :to="'/addressEdit/edit/'+item.id"><span class="icon-edite" ></span></router-link>
				</div>
	  		</mt-cell-swipe>

	  		<div slot="bottom" class="mint-loadmore-bottom">
                <span v-show="bottomStatus !== 'loading'" :class="{ 'rotate': bottomStatus === 'pull' }">↑</span>
              	<span v-show="bottomStatus === 'loading'">Loading...</span>
            </div>
	  	</mt-loadmore>
	</div>



	<router-link :to= "{ path: path, query: { url: '/addressList?url=%2Fmine' }}">
		<div class="empty-footer-sec">
			<span>+ </span> 新建地址
		</div>
	</router-link>

</div>

</template>

<script>
	import Vue from 'vue';
	import { CellSwipe } from 'mint-ui';
	import { MessageBox } from 'mint-ui';
	import { Loadmore } from 'mint-ui';
	Vue.component(Loadmore.name, Loadmore);
	Vue.component(CellSwipe.name, CellSwipe);

	import normalBar from '@/components/common/navbar/normalBar'

	import { mapState} from 'vuex'
 
	export default {

		data(){
			return {
				id: '123',
				isOrderAddress: false,
        path: '/addressEdit/add',
				bottomStatus: '',
        		allLoaded: false,
        		wrapperHeight: '600'
			}
		},
		computed: {
			...mapState({
				addressList: state => state.addressState.addressList,
				pageNo:  state => state.addressState.page,
				totalPage: state => state.addressState.totalPage
			}),
			empty(){
				return this.addressList.length > 0 ? false: true;
			},
      backPath () {
			  console.log(this.$route.query['url'])
        console.log('???')
        return this.$route.query['url']
      }
		},
		components: {
			normalBar
		},
		watch:{
			pageNo(val){
				if(val  >= this.totalPage) {
					this.allLoaded =  true; 
				}
			},
		},
		methods: {

			bottomTop() {
				this.getAddressList();
		        this.$refs.loadmore.onBottomLoaded();
		    },
	        handleBottomChange(status) {
	            this.bottomStatus = status;
	        },

			checkedToAddress(item){
				console.log(item);
				if(item.checkStatus== 0) return false;
				console.log(item);
				if( !this.$route.query.isOrderAddress) return false;
				let self = this;
				this.$store.dispatch('checkAddress',item).then(function(){
					self.$router.push({
						name: 'firmOrder',
						query: item
					})
				});
			},
			setDefaultAddress(id,index){
				let parma = {
					id: id,
					index: index
				}
				this.$store.dispatch('setDefaultAddress',parma)

			},
			delAddress(id,index){
				let self = this;
				
				let parma = {
					id: id,
					index: index
				}
				MessageBox.confirm('','',{
					message: '是否确认删除地址',
				}).then(function(action){
					self.$store.dispatch('delAddress',parma)
				},function(action){
					alert(action);
				})
			
			},
			getAddressList(){
				let self = this;
				this.$store.dispatch('getAddressListManager',self.isOrderAddress).then(function(){
					self.wrapperHeight = document.documentElement.clientHeight - self.$refs.wrapper.getBoundingClientRect().top;
				});
			}
		},
		mounted(){

		  if (this.utils.isWxLogin() === true &&
        (this.$store.state.user.mobilePhone === undefined ||
        this.$store.state.user.mobilePhone.length < 10)) {
		    this.path = '/userAccountSet/bindMobilePhone'
//		    this.path = "{ path: '/userAccountSet/bindMobilePhone', query: { url: '{ path: '/addressList', query: { url: '/mine' }}' }}"
      }
			let self = this;
			this.isOrderAddress = this.$route.query.isOrderAddress ? true : false;
			this.$nextTick(function(){
				self.getAddressList();
			});
		},
		destroyed(){
			this.$store.dispatch('initPageNo');
		}

	}
</script>
<style lang="scss">
	$baseFontSize: 750px;
	@function pxToRem($px) {
		@return $px / $baseFontSize * 10rem;
	}
	$defaultSize: pxToRem(24px);
	
	.empty-sec {
		padding: pxToRem(100px) 0;
		color: #666666;
		text-align: center;
	}
	.empty-footer-sec {
		padding: pxToRem(40px) 0;
		position: fixed;
		bottom: 0;
		left: 0;
		width: 100%;
		background-color: #fff;
		font-size: pxToRem(28px);
		color: #D8331D;
		text-align: center;
		border-top: 1px solid #ccc;
	}

	.address-list {
		padding-left: pxToRem(50px);
		.mint-cell-wrapper {
			padding: 0;
			.mint-cell-title {
				display: none;
			}
		}

		.mint-cell-swipe {
			.mint-cell-value {
				width: 100%;
			}
		}

		.address-sec {
			padding-left: 0;
			border: none;
			border-bottom: 1px solid #ccc;

			&.disabled {
				// background-color: rgba(0,0,0,.2);
				color: #ccc;
				.big-size {
					color: #ccc;
				}
			}
		}
		.mint-cell-swipe-buttongroup {
			display: flex;
		}

		.mint-cell-swipe-button {
			width: pxToRem(110px);
			vertical-align: top;
			color: #fff;
			line-height: 1.2em;
			text-align: center;
			display: inline-flex;
			align-items: center;
			justify-content: center;
		}
	}

</style>


