<template>
	
	<div class="">
		<normalBar 
		title="我的订单"
		url= '/Mine'
		></normalBar>

		<div class="order-tab">
			<span class="tab-item" :class="{'active': status==0}" @click="tabChange(0)">全部</span>
			<span class="tab-item" :class="{'active': status==1}" @click="tabChange(1)">待付款</span>
			<span class="tab-item" :class="{'active': status==2}" @click="tabChange(2)">待收货</span>
		</div>
		
		<div class="empty" v-if="orderList.length <= 0" v-cloak>
			<p>您还没有订单哦</p>
		</div>
		<div class="order-list-sec" :style="{ height: wrapperHeight + 'px' }" ref="wrapper" v-else>
			<mt-loadmore 
	  				:bottom-method="bottomTop" 
	  				@bottom-status-change="handleBottomChange" 
	  				:bottom-all-loaded="allLoaded"
	  				:autoFill = "false"
                	ref = "loadmore"
  				>

			<div class="bg-grey-sec">
				<div class="order-items" v-for="(item,itemIndex) in orderList">
					<div class="gift-sec order-sec"> 
						<div class="gift-header">
							<span>
								订单号：{{item.merchantId}}
							</span>
							
						</div>
						<div class="gift-body">
							<div class="gift-item" v-for="(proList,index) in item.wapOrderListVos">
							 <!-- v-for="order in proList.orderSkuVos" -->
								<div>
									<div class="media" 
										v-if="proList.orderSkuVos.length > 1" 
										v-cloak

										@click="goOrderDetails(item.merchantId)"
									>
										<div class="media-left img-list">
											<div class="img-wrap">
												<img :src="order.imgUrl" v-for="(order,orIndex) in proList.orderSkuVos">
											</div>
										</div>
										<div class="media-right">
											<div>
												<p>a礼盒{{index==0 ? '1' : '2'}}</p>
												<p class="text-red">{{proList.statusDesc}}</p>
											</div>
										</div>
									</div>

									<div class="media" 
									v-else 
									v-cloak 
									v-for="order in proList.orderSkuVos"
									@click="goOrderDetails(item.merchantId)"
									>
										<div class="media-left">
											<img :src="order.imgUrl" alt="">
										</div>
										<div class="media-content" >
											<div>
												<p class="m-title">
													<span>{{order.skuName}}</span>
												</p>
												<span class="m-subTitle text-grey">{{order.size}}</span>
											</div>
										</div>

										<div class="media-right">
											<div>
												<p>礼盒{{index==0 ? '1' : '2'}}</p>
												<p class="text-red">{{proList.statusDesc}}</p>
											</div>
										</div>
									</div>

									
									<div class="order-foot" v-if="proList.status == 21 || proList.status == 20">
										<button class="default-btn" type="button" @click="checkRefund(proList.id)">退款详情</button>
									</div>
									<div class="order-foot" v-if="proList.status == 5">
										<button class="default-btn" type="button" @click="checkReceive(proList.id,itemIndex,index)">确认收货</button>
									</div>
									<!-- <div class="order-foot" 
										v-if="proList.cancelStatus == 1"
									>
										<button class="default-btn" type="button" @click="checkCancel(proList.id)">取消订单</button>
									</div>
 									-->
								</div>
							</div>
						</div>
						<div class="order-foot" v-if="item.status == 13">
							<span >
								<span>
									¥{{item.totalOrderAmount | filterPrice}}
								</span>
								<countDown
									:timer = 'item.payTime'
									:payNo = 'item.payNo'
								></countDown>
							</span>
						</div>
					</div>
				</div>
			</div>
			<div slot="bottom" class="mint-loadmore-bottom">
                <span v-show="bottomStatus !== 'loading'" :class="{ 'rotate': bottomStatus === 'pull' }">↑</span>
              	<span v-show="bottomStatus === 'loading'">Loading...</span>
            </div>
			</mt-loadmore>
		</div>

	</div>

	


</template>


<script>
	import Vue from 'vue';
	import { Picker } from 'mint-ui';
	import { Popup } from 'mint-ui';
	import { Loadmore } from 'mint-ui';
	Vue.component(Loadmore.name, Loadmore);
	Vue.component(Popup.name, Popup);
	Vue.component(Picker.name, Picker);



	import normalBar from '@/components/common/navbar/normalBar'
	import countDown from '@/components/order/countdown'


	import api from '@/api/api'

	export default {
		data(){
			return {
				status: 0,//状态 0 全部 1 待付款 2待收货
				page: 0,
				pageSize: 10,
				totalPage: 0,
				orderList: [],

				bottomStatus: '',
        		allLoaded: false,
        		wrapperHeight: '600'
			}

		},
		components: {
			normalBar,
			countDown
		},
		watch:{
			page(val,oldVal){
				if(val >= this.totalPage){
					this.allLoaded = true;
				}else {
					this.allLoaded = false;
				}
			}
		},
		mounted(){
			let self  =this;
			self.status = self.$route.params.status;

			this.getOrderList(this.status).then(function(){
				self.wrapperHeight = document.documentElement.clientHeight - self.$refs.wrapper.getBoundingClientRect().top;
			});

		},
		methods:{
			getOrderList(){
				let self = this;
				return new Promise(function(resolve,reject){
						api.post('api/appclient/v1/order/list',{
							status: parseInt(self.status),
							pageNo: self.page+1,
							pageSize: self.pageSize
						},function(json){
							let res = json.body.data;
							self.page ++;
							self.totalPage =  res.totalPage;
							self.orderList = self.page <=1 ? res.itemList :  self.orderList.concat(res.itemList);	
							resolve();				
						})
				})
				
			},
			tabChange(type){
				this.status = type;
				this.page = 0;
				this.getOrderList();
				this.$router.push({
					path: '/orderManager/'+this.status
				})
			},
			bottomTop() {
				this.getOrderList();
		        this.$refs.loadmore.onBottomLoaded();
		    },
	        handleBottomChange(status) {
	            this.bottomStatus = status;
	        },
	        goOrderDetails(mchId){
				this.$router.push({path: '/orderDetails/'+mchId})
	        },
	        checkRefundDetails(orderId){
				this.$router.push({
					path: '/orderRefundDetails/'+orderId
				})
	        },
	        checkCancel(orderId){
	        	this.$router.push({
					path: '/orderCancel/'+orderId
				})
	        },
	        checkReceive(orderId,itemIndex,index){//确认收货
	        	console.log(itemIndex);
	        	let self = this;
	        	api.post('api/appclient/v1/order/signOrder/'+orderId,'',function(res){
					self.orderList[itemIndex].wapOrderListVos[index].status = 16;
					self.orderList[itemIndex].wapOrderListVos[index].statusDesc = "statusDesc";
	        	})
	        }

		}




	}



</script>


<style lang="scss">
	
	$baseFontSize: 750px;
	@function pxToRem($px) {
		@return $px / $baseFontSize * 10rem;
	}
	$defaultSize: pxToRem(24px);
	
	.empty {
		margin: pxToRem(100px) 0;
		text-align: center;
	}

	.bg-grey-sec {
		padding-bottom:pxToRem(40px);
		background-color: #eee;
		
		&:first-of-type {
			.order-items {
				margin-top: pxToRem(40px);
			}
		}
	}
	.order-tab {
		display: flex;
		justify-content: space-around;
		background-color: #fff;
		.tab-item {
			display: inline-block;
			padding: pxToRem(25px) pxToRem(10px);

			&.active {
				color: #D4B675;
				border-bottom: 2px solid #D4B675;
			}
			
		}
	}
	.order-list-sec {
		.mint-loadmore {
			background-color: #eee;
		}

	}

	
	.order-items {
		padding-left: pxToRem(50px);
		background-color: #fff;
	}
	
	.order-sec {
		background-color: #fff;
		.gift-header {
			padding: 0 pxToRem(50px) 0 0;
			text-align: center;
			line-height: pxToRem(66px);
			color: #030303;
			display: flex;
			justify-content: space-between;
			align-items: center;
			height: pxToRem(110px);
		}

		.media{
			padding: pxToRem(34px) pxToRem(54px) pxToRem(34px) 0;
			border-top: 1px solid #eeeeee;

			.media-left {
				width: pxToRem(132px);
				height: pxToRem(132px);
			}

			.img-list {
				display: inline-flex;
				flex: 1;

				.img-wrap {
					width: 90%;
					white-space: nowrap;
					overflow: auto;
				}
				img {
					width: pxToRem(132px);
					height: pxToRem(132px);
					display: inline-block;
					&:not(:last-of-type){
						margin-right: pxToRem(20px);
					}
				}
			}
			
			.media-content {
				padding-left: pxToRem(20px);
				display: inline-flex;
			    align-items: center;
			    overflow: hidden;
			    white-space: nowrap;
			    text-overflow: ellipsis;
			    &>div {
					width: 100%;
			    }
			}

			.media-right {
				display: inline-flex;
			    align-items: center;
			    text-align: right;
			}
			
		}
	}
	
	.order-foot {
		padding: pxToRem(20px) pxToRem(50px);
		border-top: 1px solid #CCCCCC;
		text-align: right;
	}
</style>


