<template>
	
<div class="default-bg">
	<normalBar title="选择付款方式"></normalBar>
	
	<ul class="pay-list">
		<li>
			<div class="pay-items">
				<span class="checkbox-group">
					<input type="radio" class="input-checkbox" v-model="payTypes"  value="8">
					<span class="icon-checkbox"></span>
				</span>
				<div>
					<span class="icon-wx"></span>
					微信安全支付
				</div>
			</div>
		</li>
		<li>
			<div class="pay-items">
				<span class="checkbox-group">
					<input type="radio" class="input-checkbox" v-model="payTypes" value="2">
					<span class="icon-checkbox"></span>
				</span>
				<div>
					<span class="icon-zfb"></span>
					支付宝支付
				</div>
			</div>
		</li>
	</ul>

	<button type="button" class="btn-sure" @click="payOrder">确认</button>
</div>

</template>

<script>
	import normalBar from '@/components/common/navbar/normalBar'
	import pingpp from 'pingpp-js'
	import api from '@/api/api'
	import { mapState } from 'vuex'

	export default {
		data(){
			return {
				payTypes: ''
			}
		},
		computed: {
      isWxLogin () {
        return this.utils.isWxLogin()
      }
    },
		components: {
			normalBar
		},
		methods: {
			payOrder(){
				let self = this;
				let payNo = self.$route.params.payNo;

				if(self.isWxLogin && self.payTypes == 2){
					self.$router.push({
						path: '/payZfb/'+payNo
					})
				}else {
					api.post('api/appclient/v1/payment/payCharge',{
						cancelUrl: window.location.origin+'/#/paySucSure/'+payNo,//	取消支付返回的url	string	
						payNo: payNo,//	支付号	string	
						successUrl:window.location.origin+'/#/paySucSure/'+payNo,
						payType: self.payTypes
					},function(json){

						let charge = json.body.data;
						pingpp.createPayment(charge, function(result, err){
						    console.log(result);
						    console.log(err.msg);
						    console.log(err.extra);
						    if (result == "success") {
						    	alert(12);
						    	self.$router.push({
									path: window.location.origin+'/paySuccess/'+payNo
								})
						        // 只有微信公众账号 wx_pub 支付成功的结果会在这里返回，其他的支付结果都会跳转到 extra 中对应的 URL。
						    } else if (result == "fail") {
						        // charge 不正确或者微信公众账号支付失败时会在此处返回
						        self.$router.push({
									path: window.location.origin+'/paySuccess/'+payNo
								})
						    } else if (result == "cancel") {
						        // 微信公众账号支付取消支付
						       self.$router.push({
									path: window.location.origin+'/paySuccess/'+payNo
								})
						    }
						});
					})
				}
				
			}
		}

	}
</script>
<style lang="scss">
	$baseFontSize: 750px;
	@function pxToRem($px) {
		@return $px / $baseFontSize * 10rem;
	}
	$defaultSize: pxToRem(24px);
	
	.pay-list {
		margin-top: pxToRem(20px);
		padding-left: pxToRem(50px);
		background-color: #fff;

		li {
			padding: pxToRem(40px) 0;

			.pay-items {
				display: flex;
				align-items: center;

				.checkbox-group {
					width: pxToRem(36px);
					height: pxToRem(38px);
				}
			}
			
			&:not(:last-of-type){
				border-bottom: 1px solid #BDBDBD;
			}
		}
	}

	.btn-sure {
		margin: pxToRem(100px) auto;
		display: block;
		width: pxToRem(656px);
		height: pxToRem(100px);
		color: #fff;
		background-color: #D8331D;
		border: none;
		font-size: pxToRem(28px);
	}


</style>


