import api from '../api'

export default {
  getHomeCakeList (data, successCallback, errCallback = function () {}, isShowErrMsg = true) {
    api.get('api/appclient/v1/product/index', data, successCallback, errCallback, isShowErrMsg)
  },
  getBanner (data, successCallback, errCallback = function () {}, isShowErrMsg = true) {
    api.get('api/appclient/v1/product/banner', data, successCallback, errCallback, isShowErrMsg)
  }
}



