<template>
  <div id="deliveryArea">

    <div class="deliveryArea-header">
      <img src="../../../assets/icon/close2.png" class="deliveryArea-header-close" @click="close"/>
      <!--<router-link to="/register" slot="right">-->
        <!--<span class="loginEntrance-header-register">注册</span>-->
      <!--</router-link>-->
      <div class="deliveryArea-header-title">查看配送区域</div>
      <div></div>

    </div>
    <hr style="" class="deliveryArea-hr"/>


    <div class="deliveryArea-search">
      <div class="deliveryArea-search-textfield">
        <img src="../../../assets/icon/search.jpg" id="deliveryArea-search-img"/>
        <input type="text"
               style="color:#191919"
               v-model="addr"
               placeholder="请输入地址查看是否在配送范围内"
               @focus="lableOnFocus"
               @blur="lableOnBlur"
               class="deliveryArea-search-lable"/>

      </div>
    </div>
    <div class="deliveryArea-content">
      <div id="map-container"></div>
    </div>

    <div class="deliveryArea-content deliveryArea-content-locationView" v-if="showSearchView === true" @click="hideEvent">
      <div class="deliveryArea-content-resultView">
        <div v-for="address in results" class="deliveryArea-content-addr" @click="selectedAddr(address)">

          <div style="margin-left: 20px;margin-right: 20px; " class="deliveryArea-content-lable">{{address.name}} - {{address.address}}</div>
        </div>
      </div>
    </div>


  </div>
</template>

<script>
  //  import { MessageBox } from 'mint-ui'

  import mapHelper from '../../../js/mapHelper'
  import api from '../../../api/common/api.common'
  import config from '../../../config'// 配置

  export default {
    name: 'deliveryArea',
    data () {
      return {
        map: {},
        addr: '',
        showSearchView: false,
        currentCity: {},
        results: [],
        marker: undefined
      }
    },
    mounted () {
      this.init()

      var self = this
      api.getStationList({},function (respond) {
        var models = respond.body.data.model
        for (var x in models) {
          var coordVals = models[x]
          var polygon = new BMap.Polygon(self.getPolygonArr(coordVals), {strokeColor:"#3d2d23", fillColor: '#3d2d23', strokeWeight:1, strokeOpacity: 0.5, fillOpacity: 0.2});
          self.map.addOverlay(polygon);
        }
      })
    },
    watch: {
      addr ( newVal,oldVal ) {
        console.log(newVal)
        if (newVal.length === 0) {
          this.showSearchView = false
        } else {
          this.showSearchView = true
        }

        let env = process.env.NODE_ENV
        var url = config[env].baiduMap + '/place/v2/search'

        var data = {}
        data.q = newVal
        data.region = this.currentCity.city
        data.output = 'json'
        data.ak = 'EbtZM9cwRqxIVALlP9ZoID0AS0Txnwen'
        var self = this

        this.$Vue.http.get(url, {params:data}).then(function (respond) {
          self.results = respond.body.results
        }, function (errRsp) {
          console.log(errRsp)
        })
      }
    },
    destroyed () {
    },
    computed: {
    },
    methods: {
      lableOnBlur () {
        this.showSearchView = false
      },
      selectedAddr (add) {
        this.map.centerAndZoom(new BMap.Point(add.location.lng,add.location.lat), 13)
        var marker = new BMap.Marker(new BMap.Point(add.location.lng,add.location.lat))
        this.map.addOverlay(marker)
        this.map.removeOverlay(this.marker)
        this.addr = add.name
        this.marker = marker
//        this.map。
      },
      hideEvent () {
        this.showSearchView = false
      },
      lableOnFocus () {
        this.showSearchView = true
      },
      close () {
//        console.log(document.getElementsByClassName('mint-tabbar')[0].style.display)
        this.$router.go(-1)
      },
      init(){
        // 百度地图API功能
         var map = new BMap.Map("map-container");    // 创建Map实例
         map.centerAndZoom(new BMap.Point(113.96055514892, 22.544916221614), 11);  // 初始化地图,设置中心点坐标和地图级别
         map.addControl(new BMap.MapTypeControl());   //添加地图类型控件
         map.setCurrentCity("深圳市");          // 设置地图显示的城市 此项是必须设置的
         map.enableScrollWheelZoom(true);     //开启鼠标滚轮缩放
        this.map = map
        var self = this

        mapHelper.getCurrentLocation(function (respond) {
          if(self.marker !== undefined) {
            return
          }

          map.centerAndZoom(new BMap.Point(respond.longitude,respond.latitude), 11)
          map.setCurrentCity(respond.city)
//          var point = BMap.Point(respond.longitude,respond.latitude)
          var maker = new BMap.Marker(new BMap.Point(respond.longitude,respond.latitude))
          map.addOverlay(maker)
          self.currentCity = respond
          self.marker = maker
        })

      },
      getPolygonArr (coords) {
        var coordsArr = [];
        coords.forEach( function(c) {
          coordsArr.push(new BMap.Point(c.longitude, c.latitude));
        });
        return coordsArr;
      }
    }
  }
</script>



<style lang="scss">
  $baseFontSize: 750px;
  $defaultColor: #191919;
  @function pxToRem($px) {
    @return $px / $baseFontSize * 10rem;
  }
  $defaultSize: pxToRem(24px);

  #deliveryArea {
    position: fixed;
    top: 0;
    left:0;
    right:0;
    bottom: 0;
  }

  .deliveryArea-header {
    position: fixed;
    height: pxToRem(100px);

    top: 0px;
    left: pxToRem(30px);
    right: pxToRem(30px);

    display: flex;
    justify-content: space-between;
    align-items: center;

  }
  .deliveryArea-header-close {
    width: pxToRem(20px);
  }

  .deliveryArea-header-title {
    font-size: pxToRem(24px);
    color: #191919;
  }

  .deliveryArea-search {
    position: fixed;
    height: pxToRem(134px);
    left:0;
    right:0;
    top:pxToRem(100px);
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .deliveryArea-content {
    position: absolute;
    top: pxToRem(234px);
    left:0;
    right:0;
    bottom:0px;
  }
  .deliveryArea-content-locationView {
    z-index: 666;
    background-color: transparent;
  }

  #map-container {
    position: absolute;
    top: 0;
    left:0;
    right:0;
    bottom:0;
    /*z-index: 100;*/
    /*margin-top: pxToRem(234px);*/
    height: 100%;
    /*!*height: pxToRem(970px);*!*/
    /*background-color: #26a2ff;*/
  }

  .deliveryArea-hr {
    position: absolute;
    top: pxToRem(100px);
    left:0;
    right:0;
    height: 1px;
    background-color: #d8d8d8;
  }

  .deliveryArea-search-textfield {

    height: pxToRem(66px);
    width: 86.666666666%;
    border: solid 1px #666666;
    display: flex;
    justify-content: flex-start;
    align-items: center;
  }

  #deliveryArea-search-img {
    width: pxToRem(35px);
    margin-left: pxToRem(20px);
  }
  .deliveryArea-search-lable {
    color: #9b9b9b;
    margin-left: pxToRem(20px);
  }
  .deliveryArea-search-lable {
    width: 70%;
  }

  .deliveryArea-content-resultView {
    background-color: transparent;
    margin-top: pxToRem(10px);
    margin-left: pxToRem(50px);
    margin-right: pxToRem(50px);
    height: pxToRem(600px);

    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    overflow: scroll;
    flex-direction: column;
  }
  .deliveryArea-content-addr {
    color: #191919;
    font-size: pxToRem(24px);
    margin-bottom: pxToRem(10px);
    background-color: white;
    width: 100%;
  }
  .deliveryArea-content-lable {
    background-color: white;
    margin-top: pxToRem(20px);
    margin-bottom: pxToRem(20px);
  }
</style>


