<template>
	
	<div class="default-bg">
		<div class="flex-bar">
			<span class="icon-goback" onClick= "history.back();"></span>
			<div class="flex-title">
				<span class="vt-middle">申请取消订单</span>
			</div>
			<div class="flex-right" @click="cancelSubmit">提交</div>
		</div>
		<div class="reason-sec" @click="popupVisible=true">
			<span class="text-grey" >
				{{reasonText ? reasonText[0] : '请选择原因' }}
			</span>
			<span class="icon-arrow-right"></span>
		</div>
		<div class="cancel-tips">
			<p>当前订单取消后将无法恢复</p>
			<p>订单若已付金额，退款将原路返回，请注意返回时间。</p>
		</div>


		<mt-popup
		  	v-model="popupVisible"
		  	position="bottom"
		  	class="reason-picker"
		  	>

		  	<mt-picker 
		  	:slots="slots" 
		  	@change="onValuesChange"
		  	:showToolbar= "true"
		  	:visibleItemCount = '3'
		  	>
		  		<span @click="popupVisible=false">取消</span>
		  		<span @click="checkReason">确定</span>
		  	</mt-picker>
		</mt-popup>

	</div>

	


</template>


<script>
	import Vue from 'vue';
	import { Toast } from 'mint-ui';
	import { Picker } from 'mint-ui';
	import { Popup } from 'mint-ui';
	Vue.component(Popup.name, Popup);
	Vue.component(Picker.name, Picker);

	import api from '@/api/api'

	export default {

		data(){
			return {
				popupVisible: false,
				reasonText: '',
				pickerCheck: '',
				slots: [
			        {
			          flex: 1,
			          values: ['不想买了','商品买错','地址写错'],
			          className: 'slot1',
			          textAlign: 'center',
			          showToolbar: true
			        }
			    ],
			}
		},
		methods: {
			onValuesChange(picker,values){
				this.pickerCheck = values;
				console.log(values);
			},
			checkReason(){
				this.reasonText = this.pickerCheck;
				this.popupVisible=false;
			},
			cancelSubmit(){
				let self = this;
				if(!this.reasonText){
					Toast('请选择原因')
					return false;
				}
				let orderId = this.$route.params.orderId;
				api.post('api/appclient/v1/order/cancel/'+orderId,{orderId:orderId},function(res){
					if(res.body.meta.code == '00000'){
						self.$router.go(-1);
					}
					
				})
			}
		}
	}

</script>


<style lang="scss">
	
	$baseFontSize: 750px;
	@function pxToRem($px) {
		@return $px / $baseFontSize * 10rem;
	}
	$defaultSize: pxToRem(24px);



	.flex-bar {
		padding: 0 pxToRem(30px);
		background-color: #fff;
		display: flex;
		justify-content: space-around;
		height: pxToRem(100px);
		line-height: pxToRem(100px);
		border-bottom: 1px solid #eee;
	}

	.flex-title {
		display: flex;
		align-items: center;
		justify-content: center;
		width: 100%;
		background-color: rgba(255,255,255,0.90);
	}
	
	.flex-right {
		width: pxToRem(70px);
		color: #666666;
	}

	.reason-sec {
		padding: pxToRem(46px) pxToRem(50px);
		display: flex;
		justify-content: space-between;
		width: 100%;
		background: #fff;
	}

	.cancel-tips {
		padding: pxToRem(40px) pxToRem(50px);
		p {
			&:not(:first-of-type) {
				margin-top: pxToRem(10px);
				color: #666;
			}
		}
	}

	.reason-picker {
		width: 100%;
	}
</style>


