<template>
	<div class="default-panel">
		
		<normalBar title="订单详情"></normalBar>
		<div class="grey-bg-sec pd40">
			<div class="order-header-sec">
				<div class="address-sec">
					<div class="address-content">
						<p class="title">收货地址</p>
						<p class="big-size">{{recipientVo.name}}<span class="last">收</span></p>
						<p>
							<!-- {{recipientVo.provinceName}} 
							{{recipientVo.cityName}}
							{{recipientVo.regionalismName}}   -->
							{{recipientVo.address}}
						</p>
						<p>{{recipientVo.mobile}}</p>
					</div>
				</div>
				<div class="timer-sec">
					<ul class="order-timer-list">
						<li><span class="text-grey">下单时间</span><span>2017／07／13  17:30</span></li>
						<li><span class="text-grey">订单号</span><span>{{merchantId}}</span></li>
						<li><span class="text-grey">付款方式</span><span>{{payModesDesc}}</span></li>
						<li><span class="text-grey">订单总计</span><span>¥{{payAmount | filterPrice}}</span></li>
					</ul>
				</div>
				<div class="price-sec">
					<div>
						<span class="text-red">
							<p><span class="label">实付款</span> ¥{{payAmount | filterPrice}}</p>
							<p v-if="status == 11"><span class="label">货到应付</span> ¥{{amount  | filterPrice}}</p>
						</span>
					</div>
					<span v-if="status == 13">
						<countDown
							:timer = 'payTime'
							:payNo = 'payNo'
						></countDown>
					</span>
				</div>
			</div>
		</div>
		
		<div class="grey-bg-sec"  v-for="(item,index) in giftBoxVoList">
			<div class="pro-sec">
				<div class="gift-sec">
					<div class="gift-header">礼盒{{index == 0 ? '1':'2'}} <span class="text-red">{{item.statusDesc}}</span></div>
					<div class="gift-body">
						<div class="media" v-for="order in item.orderSkuVos">
							<div class="media-left">
								<img :src="order.imgUrl" alt="">
							</div>
							<div class="media-content">
								<p class="m-title">
									<span>{{order.skuName}}</span>
									<span class="m-small">X{{order.num}}</span>
								</p>
								<span class="m-subTitle text-grey">{{order.size}}</span>
								<div class="m-operate">
									<div class="m-operate-cont">
										<span class="price text-red">¥{{order.discountPrice | filterPrice}}</span>
										<span class="text-grey" v-if="order.goodsType == 1">
											生日牌：{{order.chocoBoard ? order.chocoBoard : '无'}}
										</span>
										<span class="text-grey" v-if="order.goodsType == 2 && order.skuName == '数字蜡烛'">
											数字蜡烛：{{order.chocoBoard ? order.chocoBoard : '无'}}
										</span>
									</div>
								</div>
							</div>
						</div>
					</div>
				</div>
				<div class="order-details-foot">
					<div  class="details-info">
						<p><span class="text-grey">{{item.deliveryType}}:</span><span>{{item.deliveryTime}}</span></p>
					</div>

					<button type="button" class="default-btn" v-if="item.status == 5" @click="checkReceive(order.id)">确认收货</button>
					<button type="button" class="default-btn" v-if="giftBoxVoList.length>1&&item.cancelStatus == 1" @click="orderCancel(item.id)">取消订单</button>
					<button type="button" class="default-btn" v-if="item.status == 20 || item.status == 21" @click="orderCancel(item.id)">退款详情</button>
				</div>
			</div>
		</div>
		<div class="order-type-foot" @click="orderCancel" v-if="giftBoxVoList.length>1 && giftBoxVoList[0].cancelStatus == 1">
			取消订单
		</div>
	</div>


</template>

<script>
	import normalBar from '@/components/common/navbar/normalBar'
	import countDown from '@/components/order/countdown'


	import api from '@/api/api'
	

	export default {
		data(){
			return {
				recipientVo: {},//地址
				giftBoxVoList: [],//礼盒列表
				cancelStatus: 0,//是否可以取消
				amount: 0,//应收金额
				couponAmount: 0,//券优惠金额
				payAmount: 0,//支付金额

				status: 0 ,//订单状态
				statusDesc: '生产中',//订单状态描述
				totalOrderAmount: 0,//订单总金额描述


				merchantId:'',//	商户订单号	string	@mock=XF2017071213582463
			    payAmount:'',//	支付金额	number	@mock=600
			    payModesDesc:'',//	支付方式描述	string	@mock=在线支付
			    payModesId:'',//	支付id
			    payTime: '00:00',//倒计时时间
			    payNo: '',//支付号


			}
		},
		components: {
			normalBar,
			countDown
		},
		mounted(){
			this.getDetails();
		},
		methods: {
			getDetails(){
				let self = this;
				let mchId = self.$route.params.mchId;
				api.get('api/appclient/v1/order/detail/'+mchId,'',function(json){
					let res = json.body.data;;

					self.recipientVo = res.recipientVo;
					self.giftBoxVoList = res.giftBoxVoList;
					self.cancelStatus = res.cancelStatus;
					self.amount = res.amount;
					self.couponAmount = res.couponAmount;
					self.payAmount = res.payAmount;
					self.status = res.status;
					self.statusDesc = res.statusDesc;
					self.totalOrderAmount =res.totalOrderAmount;

					self.merchantId = res.merchantId;
					self.payAmount = res.payAmount;
					self.payModesDesc = res.payModesDesc;
					self.payModesId = res.payModesId;

					self.payNo = res.payNo;

					self.payTime = res.payTime;
				})
			},
			orderCancel(orderId){
				let self = this;
				this.$router.push({
					path: '/orderCancel/'+orderId
				})
			},
			checkReceive(orderId){
				console.log(orderId);
				api.post('api/appclient/v1/order/signOrder/'+orderId,'',function(res){
					this.$router.push({
						path: '/orderManager/0'
					})
	        	})
			}
		}


	}


</script>

<style lang="scss">

$defaultColor: #191919;
$baseFontSize: 750px;

@function pxToRem($px) {
	@return $px / $baseFontSize * 10rem;
}

$defaultSize: pxToRem(24px);

.grey-bg-sec.pd40 {
	padding-bottom: pxToRem(40px);
}


.order-header-sec {
	padding-left: pxToRem(50px);
	background-color: #fff;
	.address-sec {
		padding-left: 0;

		.address-content {
			max-width: 100%;
		}
	}
}

.order-timer-list {
	padding: pxToRem(20px) 0;
	border-bottom: 1px solid #ccc;

	li {
		padding: pxToRem(10px) 0;

		.text-grey {
			display: inline-block;
			width: pxToRem(120px);
			color: #666666;
		}
	}
}


.price-sec {
	padding: pxToRem(25px) pxToRem(50px) pxToRem(25px) 0;
	display: flex;
	justify-content: space-between;
	
	.label {
		display: inline-block;
		width: pxToRem(100px);
	}
	
}

.pro-sec {
	padding: 0 0 0 pxToRem(50px);
	background-color: #fff;
	
	.gift-sec {
		background-color: #fff;
		.gift-header {
			padding: pxToRem(36px)  pxToRem(50px)  pxToRem(36px) 0;
			display: flex;
			justify-content: space-between;
			color: #030303;
		}

		.media{
			padding: pxToRem(34px) pxToRem(54px) pxToRem(34px) 0;
			border-top: 1px solid #eeeeee;

			.media-left {
				width: pxToRem(132px);
				height: pxToRem(132px);
			}
			.m-operate {
				padding-right: 0;
			}
		}
	}

	.order-details-foot {
		padding: pxToRem(36px) pxToRem(36px) pxToRem(36px) 0;
		display: flex;
		justify-content: space-between;
		align-items: center;
		// height:pxToRem(100px);
		border-top: 1px solid #eeeeee;
		color: #000;
		
		.details-info {
			p {
				padding: pxToRem(3px) 0;
			}
		}

		.text-grey{
			margin-right: pxToRem(20px);
			color: #666;
		}
	}
}


.order-type-foot {
	position: fixed;
	bottom: 0;
	left: 0;
	width: 100%;
	line-height: pxToRem(110px);
	text-align: center;
	background-color: #fff;
	font-size: pxToRem(28px);
}

</style>


