<template>
	
<div>
	<div class="paySuc-sec">
		<div class="suc-img">
			<img src="../../assets/images/paySucSure.png" alt="">
		</div>
		<div class="suc-operate-group">
			<span class="op-items" @click="checkPaySec">已完成支付</span>
			<span class="op-items" @click="checkPaySec">取消并返回</span>
		</div>
	</div>
</div>
</template>
<script>
	import normalBar from '@/components/common/navbar/normalBar'
	import api from '@/api/api'
	
	export default {
		components: {
			normalBar
		},
		methods:{
			checkPaySec(){
				let self = this;
				let payNo = this.$route.params.payNo;
				api.post('api/appclient/v1/payment/chargeResult',{
					payNo: payNo
				},function(json){
					let res = json.body;
					if(res.result){
						self.$route.push({
							path: '/paySuccess/'+payNo
						})
					}else {
						self.$route.push({
							path: '/orderDetails/'+res.merchantId
						})
					}
					
				})
			}
		}

	}
</script>
<style lang="scss">
	$baseFontSize: 750px;
	@function pxToRem($px) {
		@return $px / $baseFontSize * 10rem;
	}
	$defaultSize: pxToRem(24px);
	
	.paySuc-sec {
		text-align: center;
		color: #9B9B9B;
		
		.tips {
			display: block;
			margin-top: pxToRem(40px);
		
		}
	}
	.suc-img {
		margin-top: pxToRem(160px);
		display: inline-block;
		width: pxToRem(320px);
		height: pxToRem(320px);
		color: #fff;
		position: relative;

		.text {
			display: block;
			width: 100%;
			position: absolute;
			top: pxToRem(50px);
			left: 0;
			font-size: pxToRem(28px);


		}
		img {
			width: 100%;	
		}
	}

	.suc-operate-group {
		margin-top: pxToRem(100px);
		font-size: 0;
		.op-items {
			display: inline-block;
			background-color: #fff;
			padding: pxToRem(20px) pxToRem(50px);
			border: 1px solid #666;
			font-size: pxToRem(24px);
			line-height: 1;
			color: #191919;

			&:not(:last-of-type) {
				margin-right: pxToRem(60px);
			}

		}
	}

	

</style>


