<template>
	
	<div class="default-bg">
		<normalBar title="退款详情"></normalBar>
		<div class="cancel-info-sec">
			<div class="info-item" :class="{active: 1 <= secondType}">
				<span class="icon-circel"></span>
				<div class="text">已发起退款</div>
			</div>
			<div class="info-item" :class="{active: 2 <= secondType}">
				<span class="icon-circel active"></span>
				<div class="text">已审核通过</div>
			</div>
			<div class="info-item" :class="{active: 3 <= secondType}">
				<span class="icon-circel"></span>
				<div class="text">第三方支付发起退款</div>
			</div>
			<div class="info-item" :class="{active: 4 <= secondType}">
				<span class="icon-circel"></span>
				<div class="text" v-if="secondType <=4">退款成功</div>
				<div class="text" v-if="secondType == 5">退款失败</div>

			</div>
		</div>
		<div class="refund-tips">
			若退款迟迟未到账，请与客服联系：400-999-6666
		</div>
		<span class="line"></span>
	</div>

	


</template>


<script>
	import Vue from 'vue';
	import { Picker } from 'mint-ui';
	import { Popup } from 'mint-ui';
	Vue.component(Popup.name, Popup);
	Vue.component(Picker.name, Picker);
	import normalBar from '@/components/common/navbar/normalBar'
	import api from '@/api/api'

	export default {
		data(){
			return {
				refundList: [],
				secondType: 0,
			}
		},
		components: {
			normalBar
		},
		mounted(){
			let self = this;
			let orderId = this.$route.params.orderId;
			api.get('api/appclient/v1/order/refundList/'+orderId,'',function(json){
				let res = json.body;
				console.log(res);
				self.refundList = res.data;
				self.secondType = res.data[0].secondType;
			})	
		}

	}



</script>


<style lang="scss">
	
	$baseFontSize: 750px;
	@function pxToRem($px) {
		@return $px / $baseFontSize * 10rem;
	}
	$defaultSize: pxToRem(24px);


	.cancel-info-sec {
		position: relative;
		

		.info-item {
			margin-top: pxToRem(20px);
			display: flex;
			justify-content: center;
			align-items: center;
			
			.text {
				padding-left: pxToRem(25px);
				margin-left: pxToRem(40px);
				width: pxToRem(594px);
				height: pxToRem(120px);
				color: #BDBDBD;
				background-color: #fff;
				display: inline-flex;
				align-items: center;
			}

			.icon-circel {
				display: inline-block;
				width: 10px;
				height: 10px;
				border: 1px solid #ccc;
				border-radius: 50%;
				position: relative;
				background: #eee;
				z-index: 999;
			}

			&.active {
				.text {
					color: #191919;
				}
				.icon-circel {
					border-color: #D8331D;
				}
			}
		}

	}

	.line  {
		width: 1px;
		background-color: #ccc;
		position: absolute;
		left: pxToRem(58px);
		top: pxToRem(100px);
		bottom: 0;
		z-index: 0;
	}
	
	.refund-tips {
		position: absolute;
		bottom: pxToRem(50px);
		width: 100%;
		text-align: center;

	}
</style>


