<template>
	
<div class="default-panel">
	<div class="flex-bar">
		<span class="icon-goback" onClick= "history.back();"></span>
		<div class="flex-title">
			<span class="vt-middle">新增地址</span>
		</div>
		<span class="flex-right">
		</span>
	</div>
	<div class="grey-bg-sec">
		<div class="loginTips">
			<ul class="edit-list">
				<li>
					<div class="edit-item">
						<span>如此前已购买过，可直接登录</span>
						<router-link :to="'/login?url='+isFirmOrder"><span class="icon-arrow-right"> </span></router-link>
					</div>
				</li>
			</ul>
		</div>
	</div>
	<div class="grey-bg-sec">
		<div class="loginTips">
			<ul class="edit-list">
				<li>
					<div class="edit-item">
						<span style="color: #666">配送方式</span>
						<span>配送上门</span>
					</div>
				</li>
			</ul>
		</div>
	</div>
	

	<div class="address-edit-sec">
		<ul class="edit-list">
			<li>
				<div class="edit-item">
					<input type="text" class="input" v-model="name" placeholder="收货人">
				</div>
			</li>
			<li>
				<div class="edit-item">
					<input type="text" class="input" v-model="phone" maxlength="11" 
					placeholder="收货人手机"
					/>
				</div>
			</li>
			<li>
				<div class="edit-item">
				<input type="text"  class="input" v-model="area" 
					placeholder="收货城市"
					readonly="readonly" 
					@click="popupVisible=true"
					/>
					<span class="icon-arrow-right"></span>
				</div>
			</li>
			<li>
				<div class="edit-item">
					<input type="text"  class="input" v-model="address" 
					placeholder="详细地址，如街道、楼层、门牌号等"
					/>
				</div>
			</li>
		</ul>

		<ul class="edit-list">
			<li>
				<div class="edit-item">
					<input type="text" class="input" v-model="cusUser"  placeholder="下单人">
				</div>
			</li>
			<li>
				<div class="edit-item">
					<input type="text" class="input" v-model="cusPhone" maxlength="11" 
					placeholder="下单人手机"
					/>
				</div>
			</li>
			<li>
				<div class="edit-item no-bor">
					<input type="text"  class="input code-input" v-model="code" 
					placeholder="短信验证码"
					/>
					<button class="code" type="button" @click="getVaildCode">{{codeText}}</button>
				</div>
			</li>
		</ul>
	</div>

	<button type="button" class="btn-save" @click="noLoginSave">确认</button>


	<mt-popup
 		v-model="popupVisible"
 		class="address-popup"
  		position="bottom">
		<mt-picker 
		:showToolbar= true
		:slots="slots" 
		@change="onValuesChange"
		valueKey = 'value'
		>
			<span @click="popupVisible = false">取消</span><span @click="setAddressArea">确定</span>

		</mt-picker>
	</mt-popup>



</div>

</template>

<script>
	import Vue from 'vue';
	import { CellSwipe } from 'mint-ui';
	import { MessageBox } from 'mint-ui';
	import { Cell } from 'mint-ui';
	import { Popup } from 'mint-ui';
	import { Picker } from 'mint-ui';
	import { Toast } from 'mint-ui';
	Vue.component(Picker.name, Picker);
	Vue.component(Popup.name, Popup);
	Vue.component(Cell.name, Cell);
	Vue.component(CellSwipe.name, CellSwipe);

	import normalBar from '@/components/common/navbar/normalBar'
	
	import api from '@/api/api.js';
	
	export default {
		data(){
			return {
				name: '',
				phone: '',
				area: '',
				address: '',

				cusUser:'',
				cusPhone:'',
				code: '',
				
				popupVisible: false,
				pickerArea: '',

				codeText: '获取验证码',
			    disabled: false,

				slots: [
			        {
			          flex: 1,
			          values: [],
			          className: 'slot1',
			          textAlign: 'right'
			        }, {
			          divider: true,
			          content: '-',
			          className: 'slot2'
			        }, {
			          flex: 1,
			          values: [],
			          className: 'slot3',
			          textAlign: 'left'
			        }
			        , {
			          divider: true,
			          content: '-',
			          className: 'slot4'
			        }, {
			          flex: 1,
			          values: [],
			          className: 'slot5',
			          textAlign: 'left'
			        }
			    ],
			    curPro:'',
			    curCity: '',
			    regionalismId:'',
			    isFirmOrder:'',
			    path:''
			   
			}
		},
		components: {
			normalBar
		},
		watch: {
			
			phone(val){
				this.phone = val.replace(/\D/g,'');
			},
			cusPhone(val){
				this.cusPhone = val.replace(/\D/g,'');
			},

			curPro(val,oldValue){
				let self = this;
				console.log(oldValue);
				this.$nextTick(function(){
		      		self.getProvince(val).then(function(data){
						let regionArr  =[];
						for( let item in data){
							regionArr.push({
								key: item,
								value: data[item]
							})
						}
						self.slots[2].values = regionArr;
					});
				})

			},
			curCity(val,oldValue){
				let self = this;
				console.log(oldValue);
				this.$nextTick(function(){
		      		self.getRegional(val).then(function(data){
						let regionArr  =[];
						for( let item in data){
							regionArr.push({
								key: item,
								value: data[item]
							})
						}
						self.slots[4].values = regionArr;
					});
				})
				
			}


		},
		mounted(){
			let self = this;

			self.isFirmOrder =  this.$route.query.isFirmOrder ? true : false;
			self.isFirmOrder = self.isFirmOrder ? '/firmOrder': undefined;


			this.getProvince().then(function(data){
				let regionArr  =[];
				for( let item in data){
					regionArr.push({
						key: item,
						value: data[item]
					})
				}
				self.slots[0].values = regionArr;
			});
			
			
		},
		methods: {
			getVaildCode(){
				// this.codeText 
				let self = this;

				if( this.cusPhone =='' || this.cusPhone.length !=11) {
					Toast('请输入下单人手机号');
					return false;
				}

				if( this.disabled == true ) return false;
					
				api.get('api/appclient/v1/verifycode/get',{
					mobilePhone: this.cusPhone,
					verifyCodeType: 3

				},function(res){
					let seconds= 60;
					self.disabled = true;
					let timer = setInterval(function(){
						seconds --;
						self.codeText = seconds +'s';

						if(seconds <=0){
							self.codeText = '获取验证码';
							self.disabled = false;
						}
					},1000)
				})

			},
			noLoginSave(){
				let self = this;


				if(this.name == ''){
					Toast('请填写收货人');
					return false;
				} else if(this.phone=='' || this.phone.length != 11){
					Toast('请正确填写收货人手机');
					return false;
				}else if(this.area==''){
					Toast('请选择城市');
					return false;
				}else if(this.address==''){
					Toast('请填写详情地址');
					return false;
				}else if(this.cusUser==''){
					Toast('请填写下单人');
					return false;
				}else if(this.cusPhone==''){
					Toast('请填写下单人手机号');
					return false;
				}else if(this.code==''){
					Toast('请填写验证码');
					return false;
				}
				
				var params = {
					address: self.address,//	详细地址	string	
					mobilePhone: self.cusPhone,//	手机号码	string	
					ownerName: self.cusUser,//	预约人	string	
					recipientMobile: self.phone,//	收货人手机	string	
					recipientName: self.name,//	收货人姓名	string	
					regionalismId: self.regionalismId,//	行政区域ID	number	
					verifyCode: self.code,//
				};

				console.log(params);
				api.post('api/appclient/v1/user/order/login',params,function(res){

					localStorage.setItem('loginCredentials', res.body.data['loginCredentials']);
					localStorage.setItem('userIsLogin', true);
          			localStorage.setItem('userloginTime', new Date());

					self.$router.push({
						name: 'addressList',
						params: {
							isOrderAddress: self.$route.params.isOrderAddress
						}
					})
				})

				

			},
			onValuesChange(picker, values) {
		      	var self  = this;
		     	 this.$nextTick(function(){
					self.curPro = values[0] ?  values[0].key : '';
					self.curCity = values[1] ?  values[1].key : '';
					self.regionalismId = values[2] ?  values[2].key : '';
					self.pickerArea = values[2] ? values.map(function(item){
						return item.value;
					}):'';

				})
    		},
    		setAddressArea(){
    			this.area = this.pickerArea;
    			this.popupVisible = false;
    		},
    		getProvince(id){//省市
    			let self = this;
    			id = id ? id: '';
    			return new Promise(function(reslove,reject){
    				api.get('api/common/v1/baseinfo/region',{regionalismId:id},function(res){
						reslove(res.body.data);
					})
    			})
				
    		},
    		getRegional(id){
    			return new Promise(function(reslove,reject){
    				api.get('api/common/v1/baseinfo/region/'+id,{},function(res){
						reslove(res.body.data);
					})
    			})
    		}
		}

	}
</script>
<style lang="scss">
	$baseFontSize: 750px;
	@function pxToRem($px) {
		@return $px / $baseFontSize * 10rem;
	}
	$defaultSize: pxToRem(24px);
	

	.address-edit-sec {
		width: 100%;
	}

	.loginTips .edit-list {
		.edit-item {
			padding: pxToRem(50px)  pxToRem(60px) pxToRem(50px) 0;
			border-bottom: none;
		}
	}

.edit-list {
	padding-left: pxToRem(50px);
	background-color: #fff;

	.edit-item {
		border-bottom: 1px solid #666666;
	}

	.edit-item  {
		display: flex;
		justify-content: space-between;
		padding: pxToRem(60px)  pxToRem(60px) pxToRem(30px) 0;
		&.no-bor {
			border: none;
		}

		.input {
			width: 100%;

			&.code-input {
				padding-bottom:  pxToRem(30px);
				width: 65%;
				border-bottom: 1px solid #666666;
			}

		}
		input::-webkit-input-placeholder {
			color: #BDBDBD;
		}
	}
}

.code {
	display: inline-block;
	border: 1px solid #666;
	color: #BDBDBD;
	background-color: #fff;
	width: pxToRem(180px);
	height: pxToRem(76px);

}

.btn-save {
	margin: pxToRem(100px) auto;
	display: block;
	width: 87%;
	height: pxToRem(100px);
	color: #fff;
	border: none;
	line-height: 1;
	font-size: pxToRem(28px);
	background-color: #D8331D;
}

.address-popup {
	width: 100%;
}
.picker-items {
	width: 100%;
}

.flex-bar {
	padding: 0 pxToRem(30px);
	display: flex;
	justify-content: space-around;
	height: pxToRem(100px);
	line-height: pxToRem(100px);
	border-bottom: 1px solid #eee;
}

.flex-title {
	display: flex;
	align-items: center;
	justify-content: center;
	width: 100%;
	background-color: rgba(255,255,255,0.90);
}
.edite {
	display: inline-block;
	width: pxToRem(60px);
	color: #666;
}
	
</style>


