import api from '@/api/api';
import Vue from 'vue';

const GETCARTINFO = 'GETCARTINFO'
const ADDTOCART = 'ADDTOCART';
const GETCARTNUM = 'GETCARTNUM';
const GETPARTLIST  = 'GETPARTLIST';
const DELCARTITEM = 'DELCARTITEM';

const UPDATECARTITEM = 'UPDATECARTITEM';
const ADDPARTTOCART = 'ADDPARTTOCART';

const GETFIRMORDER = 'GETFIRMORDER';
const SETORDERADDRESS = 'SETORDERADDRESS';


export default {
	state: {
		counter: 0,//购物车商品总数
		shopCartList: [],//商品列表信息
		finalOrderPrice: 0,//最终订单价
		freight: 0,//运费
		totalPrice: 0,//商品总价
		itemGsTypes: [],//购物车商品类型集合

		partList: [],//配件列表

		firmOrder: {},//确认订单礼盒列表信息

		orderAddress: {},//订单地址

	},
	getters:{
		// hasCake: state => {
		// 	return state.itemGsTypes.some(function(index){ return index == 1 });
		// }
	},
	actions: {
		getCartNum({commit}){//获取购产车商品数
			api.get('api/appclient/v1/cart/getCount',{},function(res){
				var res = res.body;
				if(res.meta.code == '00000'){
					commit('GETCARTNUM',res.data.itemCount);
				}
			})
		},
		addCart({ commit },proSku){//添加商品到购物车
			return new Promise(function(resolve, reject){
				api.post('api/appclient/v1/cart/addToCart',proSku,function(res){
					var res = res.body;
					if(res.meta.code == '00000'){
						commit('ADDTOCART',proSku.num);
						resolve(res);
					}
				})
			})
		},
		updateExtra({ commit },parma){//设置数字备注及生日牌
			return new Promise(function(resolve, reject){
				console.log(parma);
				api.post('api/appclient/v1/cart/doGoodsExtra',parma,function(res){
					var res = res.body;
					if(res.meta.code == '00000'){
						resolve(res);
					}
				})
			})

		},
		delCartItem({ commit },skuPro){ //删除商品
			return new Promise(function(resolve, reject){
				console.log(skuPro);
				api.post('api/appclient/v1/cart/deleteCartItem',{sidList:skuPro.skuId},function(res){
					var res = res.body;
					if(res.meta.code == '00000'){
						commit('DELCARTITEM',{
							proSku:skuPro,
							data:res.data
						});
						resolve(res);
					}
				})
			})
		},
		getCartInfo( {commit}){//获取购物车列表信息
			return new Promise(function(resolve,reject){
				api.get('api/appclient/v1/cart/getCartInfo',{},function(res){
					var res = res.body;
					if(res.meta.code == '00000'){
						commit('GETCARTINFO',res.data)
					}
				})
			})
		},
		getPartList({ commit }){
			api.get('api/appclient/v1/product/getParts','',function(res){
				var res = res.body;
				commit('GETPARTLIST',res.data)
			})
		},
		addPartToCart({ commit,state },skuPro){//配件加入购物车
			console.log(skuPro);
			commit('ADDPARTTOCART',skuPro);


			
		},
		//减数量 加数量 是否选择
		updateCartItem( { commit},skuPro ){//
			console.log(skuPro);
			return new Promise(function(resolve,reject){
				api.post('api/appclient/v1/cart/updateCartItem',skuPro,function(res){
					var res = res.body;
					commit('UPDATECARTITEM',res.data)
					resolve(res);
				})
			})
		},
		getFirmOrderAddress( {commit }){
			//code 03206 说明用户无收货地址 code 03209 说明没有匹配到当前区域的收货地址
			return new Promise(function(resolve,reject){
				// api.get('/getfirmOrderAddress',{},function(res){
				api.get('api/appclient/v1/user/address/order',{},function(res){
					var res = res.body;
					if(res.meta.code == '00000'){
						commit('SETORDERADDRESS',res.data);
						resolve(res.meta.code)
					}else if( res.meta.code == '03206' || res.meta.code == '03209'  ){
						resolve(res.meta.code)
					}
				})
			})
			
		},
		getFirmOrder({ commit }){
			api.get('api/appclient/v1/order/getPreOrderInfo','',function(res){
				var res = res.body;
				console.log(res);
				if(res.meta.code == '00000'){
					commit('GETFIRMORDER',res.data)
				}
			})
		},
		getDeliveryTimer({ commit },parma){
			return new Promise(function(resolve,reject){
				api.get('api/appclient/v1/product/skuDeliveryTime',parma,function(res){
					var res = res.body;
					if(res.meta.code == '00000'){
						resolve(res.data);
					}
				})
			})
		},
		setOrderAddress({ commit },data){
			commit('SETORDERADDRESS',data)
		},
		confirmOrder({ commit },parma){
			return new Promise(function(resolve,reject){
				api.post('api/appclient/v1/order/submitOrder',parma,function(res){
					var res = res.body;
					if(res.meta.code == '00000'){
						resolve(res.data);
					}
				})
			})
		}
	},
	mutations:{
		[GETCARTNUM](state,num){
			state.counter = parseInt(num);
		},
		[ADDTOCART]( state, options){
			state.counter += parseInt(options);
		},
		[GETCARTINFO](state,data){
			state.shopCartList = data.cartItemVoList;//商品列表信息
			state.finalOrderPrice = data.finalOrderPrice;//最终订单价
			state.freight = data.freight;//运费
			state.totalPrice = data.totalGoodsPrice;//商品总价
			// state.itemGsTypes = data.itemGsTypes;//商品类型集合

		},
		[GETPARTLIST](state,data){
			state.partList = data;
		},
		[UPDATECARTITEM](state,data){
			state.finalOrderPrice = data.finalOrderPrice;//最终订单价
			state.freight =  data.freight;//运费
			state.totalPrice = data.totalGoodsPrice;//商品总价

		},
		updataCandle(state,value){
			state.shopCartList[0].extra.candleModel = value;
		},
		DELCARTITEM(state,{proSku,data}){
			if(proSku.index){
				state.shopCartList.splice(proSku.index,1);
			}else {
				let skuIdList = proSku.skuId;
				state.shopCartList = state.shopCartList.filter((item,index)=>{
					for(let i = 0; i< skuIdList.length ; i++){
						 if(item.sid != skuIdList[i]){
						 	return true
						 }else {
						 	return false;
						 } ;
						
					}
				})	
			}

			state.finalOrderPrice = data.finalOrderPrice;//最终订单价
			state.freight =  data.freight;//运费
			state.totalPrice = data.totalGoodsPrice;//商品总价

			
		},
		[ADDPARTTOCART](state,skuPro){
			
			// let tempData = {
	  //           "classifyName": '',
	  //           "displayName": state.partList[skuPro.index].skuName,
	  //           "favorite": false,
	  //           "goodsType": '2',
	  //           "num": 1,
	  //           "picture": null,
	  //           "pid": null,
	  //           "price": null,
	  //           "sid": skuPro.sid,
	  //           "size": null,
	  //           "extra":{
	  //               "candleModel": '', 
	  //               "chocoBoard": ''
	  //           },  
	  //           ...state.partList[skuPro.index]
	  //       }
			// console.log(tempData);
			// let shopCartList = state.shopCartList;
			
			// let flag = true,index;

			// for(let i = 0 ; i < shopCartList.length ; i++ ){
				
			// 	if(shopCartList[i].sid == skuPro.sid){
			// 		flag = true;
			// 		index = i;
			// 		break;
			// 	}else {
			// 		flag = false;
			// 	}
			// }

			// if(flag){
			// 	state.shopCartList[index].num += 1;
			// }else {
			// 	state.shopCartList.push(tempData);
			// }
		},
		[GETFIRMORDER](state,data){
			console.log(data);
			state.firmOrder = data;
		},
		[SETORDERADDRESS](state,data){
			state.orderAddress = data;
		}
	}
}  		
    	
    


