<template>
	<button type="button" class="pay-item" @click="payOrder">付款 {{countText}}</button>
</template>


<script>

	export default {
		props: ['timer','payNo'],
		data(){
			return {
				countText: '',
				counterTimer: null,
				count: 0,
			}
		},
		computed:{
			
		},
		methods: {
			payOrder(){
				let self = this;
				self.$router.push({
					path: '/payTypes/'+self.payNo
				})
			}
		},
		mounted(){
			let self = this;
			self.count = self.timer;
			self.counterTimer = setInterval(function(){
				let timers = self.count;
				
				let hours = parseInt(timers/(60*60*1000));
				let min = parseInt( (timers - hours*60*60*1000)/(1000*60));
				let sec = parseInt( (timers -  hours*60*60*1000 - min*1000*60)/1000 );



				// let min = parseInt(timers/(1000*60));
				// let sec = parseInt((timers - min*1000*60)/1000);
				// 
				min = min < 10 ? '0'+ min : min;
				sec = sec < 10 ? '0'+sec : sec;
				self.countText = min + ':' +sec;
				
				if(self.count < 0){
					clearInterval(self.counterTimer);
					return false;
				}else {
					self.count -= 1000;
				}
				
			},1000);
			
		}

	}



</script>


<style lang="scss">
	
	$baseFontSize: 750px;
	@function pxToRem($px) {
		@return $px / $baseFontSize * 10rem;
	}
	$defaultSize: pxToRem(24px);



	
</style>


