<template>
	<div id="adressBar" class="adressBar">
		<router-link :to="goBackLink" class="icon-goback"  v-show="isBack"></router-link>
		<div class="adressNav">
			<span class="adress-adress-group" @click="goChooseCity">
				<span class="icon-adress"></span>
				<span class="adressNav-text">{{currentCity.cityName}}</span>
			</span>
		</div>
		<span class="icon-nav-group" v-show="navBtnShow">
			<span class="icon-nav" @click="navShowClick"></span>
		</span>
	</div>
</template>

<script>
	import Vue from 'vue';
	import { mapState } from 'vuex'
  import bus from '../../../js/eventBus'
	export default {
		name: 'aressBar',
		props: ['isBack','navBtnShow','goBackLink','navShow'],
		data() {
			return {
        currentCity: ''
			}
		},
		computed: mapState({
			navShows: state => state.homeState.navBtnShow,
//			currentCity: state => state.user.currentCity
		}),
		components: {
		},
		methods: {
			navShowClick(){
				this.$emit('navShowClick');
			},
			goChooseCity(){
				this.$router.push({'name':'chooseCity'})
			}
			
		},
    mounted () {
		  var self = this
      var city = eval ("(" + localStorage.getItem('locationCity') + ")")
      this.currentCity = city

		  bus.$on('changeCity',function (city) {
        var currentCity = {}
        currentCity.cityName = city.cityName
        self.currentCity = currentCity
      })
    }
	}
</script>

<style lang="scss">
	$baseFontSize: 750px;
	@function pxToRem($px) {
		@return $px / $baseFontSize * 10rem;
	}

	.adressBar {
		padding: 0 pxToRem(30px);
		display: flex;
		justify-content: space-around;
		height: pxToRem(100px);
		line-height: pxToRem(100px);
	}

	.adressNav {
		display: flex;
		align-items: center;
		justify-content: center;
		width: 100%;
		background-color: rgba(255,255,255,0.90);
		font-size: 0;

		.adress-adress-group {

      display: flex;
      align-items: center;
      justify-content: center;

			font-size: pxToRem(24px);
			.icon-adress {
				margin-right: pxToRem(20px);
        width: pxToRem(20px);
			}
      .adressNav-text {

      }
		}

	}

	
</style>


